<style lang="scss" scoped>
  ul {
    margin-top: 40px;
    li {
      border: 1px solid #ccc;
      height: 40px;
      line-height: 20px;
      padding: 10px;
      width: 60%;
      min-width: 300px;
      margin-bottom: 20px;
      border-radius: 3px;
      transition: border 500ms;
      &:hover {
        border: 1px solid #0ae;
      }
    }
  }
</style>

<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item><i class="el-icon-date"></i> 案例</el-breadcrumb-item>
      <el-breadcrumb-item>drag拖拽</el-breadcrumb-item>
    </el-breadcrumb>

    <ul>
      <li v-for="item in data" draggable="true"
        @dragstart="dragstartEvent"
        @dragend="dragendEvent"
        @dragenter="dragenterEvent"
        @dragleave="dragleaveEvent"
        @dragover="dragoverEvent">
        {{item.value}}</li>
    </ul>

    <pre v-if="$root.token"><code>
      dragstartEvent(e) {
        this.dragElement = e.target;
        e.target.style.backgroundColor = '#0ae'
      },
      dragendEvent(e) {
        e.target.style.backgroundColor = '#fff'
        e.preventDefault()
      },
      dragenterEvent(e) {
        if(this.dragElement != e.target){
          e.target.parentNode.insertBefore(this.dragElement, e.target)
        }
      },
      dragleaveEvent(e) {
        if(this.dragElement != e.target){
          if(e.target == e.target.parentNode.lastElementChild || e.target == e.target.parentNode.lastChild){
            e.target.parentNode.appendChild(this.dragElement)
          }
        }
      },
      dragoverEvent(e) {
        e.preventDefault()
      }
    </code></pre>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        data: [
          { key: 1, value: '这里是列表1' },
          { key: 2, value: '这里是列表2' },
          { key: 3, value: '这里是列表3' },
          { key: 4, value: '这里是列表4' },
          { key: 5, value: '这里是列表5' },
          { key: 6, value: '这里是列表6' }
        ],
        dragElement: ''
      }
    },

    methods: {
      dragstartEvent(e) {
        this.dragElement = e.target;
        e.target.style.backgroundColor = '#0ae'
      },
      dragendEvent(e) {
        e.target.style.backgroundColor = '#fff'
        e.preventDefault()
      },
      dragenterEvent(e) {
        if(this.dragElement != e.target){
          e.target.parentNode.insertBefore(this.dragElement, e.target)
        }
      },
      dragleaveEvent(e) {
        if(this.dragElement != e.target){
          if(e.target == e.target.parentNode.lastElementChild || e.target == e.target.parentNode.lastChild){
            e.target.parentNode.appendChild(this.dragElement)
          }
        }
      },
      dragoverEvent(e) {
        e.preventDefault()
      }
    }

  }
</script>
